html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Arimo-Regular", georgia, sans-serif;
  font-size: 20px;
  color: #333333; }
  @media (max-width: 767px) {
    body {
      font-size: 18px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

svg path {
  fill: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

@font-face {
  font-family: "DidactGothic-Regular";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("DidactGothic-Regular"), url("../fonts/DidactGothic-Regular.woff2") format("woff2"), url("../fonts/DidactGothic-Regular.woff") format("woff"); }

@font-face {
  font-family: "Arimo-Regular";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("Arimo-Regular"), url("../fonts/Arimo-Regular.woff2") format("woff2"), url("../fonts/Arimo-Regular.woff") format("woff"); }

.btn {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer; }

.container {
  max-width: 1216px;
  padding: 0 15px;
  margin: 0 auto; }

.section {
  padding-bottom: 60px; }
  @media (max-width: 767px) {
    .section {
      padding-bottom: 30px; } }

.section__title {
  font-family: "DidactGothic-Regular", calibri, sans-serif;
  font-size: 48px;
  margin: 0 0 40px 0; }
  @media (max-width: 767px) {
    .section__title {
      margin-bottom: 20px;
      font-size: 32px; } }
  .section__title::after {
    content: "";
    display: block;
    width: 150px;
    margin-top: 40px;
    border-bottom: 1px solid #D8CACB; }
    @media (max-width: 767px) {
      .section__title::after {
        margin-top: 20px; } }

.about__container {
  display: flex;
  justify-content: space-between; }

.about__text {
  width: 50%; }

.header {
  padding: 30px 0; }

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 767px) {
    .header__container {
      flex-direction: column; } }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 767px) {
    .header__wrapper {
      width: 100%;
      margin-bottom: 10px; } }

.header__logo {
  display: none; }
  .header__logo--active {
    display: block;
    width: 70px;
    height: 70px; }
    @media (max-width: 767px) {
      .header__logo--active {
        width: 40px;
        height: 40px; } }

@media (max-width: 767px) {
  .header__nav {
    display: none; } }

.header__nav--opened {
  display: block; }

.header__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex; }
  @media (max-width: 767px) {
    .header__list {
      flex-direction: column; } }

.header__item {
  margin-right: 30px; }
  @media (max-width: 767px) {
    .header__item {
      margin-bottom: 10px; }
      .header__item:last-child {
        margin-bottom: 0; } }
  .header__item:last-child {
    margin-right: 0; }

.header__link {
  color: #A0969E;
  transition: color 0.3s ease-in; }
  .header__link:hover, .header__link:focus {
    text-decoration: none;
    color: #333333; }
  .header__link--active {
    color: #333333; }

.header__toggle {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  display: none;
  margin-left: auto;
  background: url("../img/icons/icon-menu.svg") center no-repeat transparent;
  background-size: contain; }
  .header__toggle--opened {
    background-image: url("../img/icons/icon-croix.svg"); }
  @media (max-width: 767px) {
    .header__toggle {
      display: block;
      width: 20px;
      height: 20px; } }

.portfolio p {
  margin: 0 0 50px 0; }

.portfolio__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  @media (max-width: 1023px) {
    .portfolio__list {
      flex-direction: column; } }

.portfolio__item {
  width: calc(50% - 25px);
  margin-bottom: 50px; }
  .portfolio__item:last-child {
    margin-bottom: 0; }
  @media (max-width: 1023px) {
    .portfolio__item {
      width: auto;
      margin-bottom: 30px; } }
  .portfolio__item:nth-child(1) .work__link, .portfolio__item:nth-child(1) .work__container {
    background-color: #F7E2D9; }
  .portfolio__item:nth-child(2) .work__link, .portfolio__item:nth-child(2) .work__container {
    background-color: #D7C1BD; }
  .portfolio__item:nth-child(3) .work__link, .portfolio__item:nth-child(3) .work__container {
    background-color: #EEDDDD; }
  .portfolio__item:nth-child(4) .work__link, .portfolio__item:nth-child(4) .work__container {
    background-color: #D9D9D9; }
  .portfolio__item:nth-child(even) .work__link:hover, .portfolio__item:nth-child(even) .work__link:focus {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0; }
  .portfolio__item:nth-child(even) .work__link:hover + .work__container,
  .portfolio__item:nth-child(even) .work__link:focus + .work__container {
    transform: translateX(-100%); }
  .portfolio__item:nth-child(even) .work__container {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 2%;
    border-bottom-left-radius: 2%; }

.work {
  position: relative; }
  @media (min-width: 1024px) {
    .work.is-hover {
      z-index: 1; } }

.work__link {
  display: block;
  padding: 80px;
  border-radius: 2%;
  background-color: #F7E2D9;
  text-align: center; }
  .work__link:hover, .work__link:focus {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0; }
  .work__link:hover + .work__container,
  .work__link:focus + .work__container {
    visibility: visible;
    transform: translateX(100%); }
  @media (max-width: 1023px) {
    .work__link {
      padding: 40px; }
      .work__link:hover + .work__container,
      .work__link:focus + .work__container {
        transform: none; } }
  @media (max-width: 767px) {
    .work__link {
      padding: 30px; } }
  .work__link img {
    border-radius: 2%;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.5); }
    @media (max-width: 767px) {
      .work__link img {
        width: 300px; } }

.work__container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  padding: 80px;
  background-color: #F7E2D9;
  border-top-right-radius: 2%;
  border-bottom-right-radius: 2%;
  transition: all .5s;
  visibility: hidden; }
  @media (max-width: 1023px) {
    .work__container {
      position: static;
      visibility: visible;
      transform: none;
      padding: 40px; } }
  .work__container h3 {
    font-family: "DidactGothic-Regular", calibri, sans-serif;
    color: #83A1D3; }
  .work__container p {
    color: #333333;
    margin-bottom: 25px; }
  .work__container h3, .work__container p {
    font-size: 18px; }
    @media (max-width: 1023px) {
      .work__container h3, .work__container p {
        font-size: 14px; } }
